/* imgPath: 'http://aqhb.ynhlxy.com:194/fileupload' //  */
import { downloadPDF } from "@/utils/exportPdfFun";

export default {
  name: "exportPdf",
  components: {},
  props: {},
  computed: {},
  filters: {},
  watch: {},
  data() {
    return {
      formData: {
        safetyMeasureRecord: [],
      },
      processLogData: [
        {
          beforeFlowNodeName: "",
          actionList: [
            {
              id: "376aa965af6f04a3afae1c1675a7792b",
