            },
          ],
        },
        {
          beforeFlowNodeName: "",
          actionList: [
            {
              id: "b4acf9a05ae8bd17488c8c82c4717705",
              operation: "",
              operationName: "",
              actionType: "approve",
              actionTypeLabel: "",
              content: "",
              images: "",
              signature: "0cf766b7849e49d987082064d40fc371",
              finish: true,
              updateTime: "2022-09-22 12:22:07",
            },
          ],
        },
        {
          beforeFlowNodeName: "",
          actionList: [
            {
              id: "6032b22184b8059f5613aa89f8a7a019",
              operation: "",
              operationName: "",
              actionType: "approve",
              actionTypeLabel: "",
              content: "",
              images: "",
              signature: "dcfad45b9ee04d22b318f33b2ce226ab",
              finish: true,
              updateTime: "2022-09-22 12:41:08",
            },
          ],
        },
        {
          beforeFlowNodeName: "",
          actionList: [
            {
              id: "5fe1caaf1422b09419555b05b096fa48",
              operation: "",
              operationName: "",
              actionType: "approve",
              actionTypeLabel: "",
              content: "",
              images: "",
              signature: "95ec31a5ab8144a899fccc5b549a1624",
              finish: true,
              updateTime: "2022-09-22 15:51:07",
            },
          ],
        },
      ],
      imgPathWater: "http://aqhb.ynhlxy.com:194/fileupload",
    };
  },
  methods: {
    getFromData() {
      /* Object.prototype.toString.call('') === '[object String]' */
      /* Object.prototype.toString.call({}) === '[object Object]' */
      /* Object.prototype.toString.call([]) === '[object Array]' */
      this.$axios
        .create({
          baseURL: "",
          timeout: 5000,
          headers: { "Content-type": "multipart/form-data" },
        })
        .get("/formDataForExportPdfUse.json")
        .then((res) => {
          this.formData = res.data.data;
          this.formData.controlsRecord.map((data) => {
            if (
              Object.prototype.toString.call(data.value) === "[object Array]"
            ) {
              let nameArr = [];
              data.value.forEach((item) => {
                nameArr.push(item.name);
              });
              data.value = nameArr.toString();
            }
            if (data.title === "") {
              data.value = data.value.info;
            }
            if (data.title === "") {
              let riskArr = [];
              data.value = data.value.choice.map((item) => {
                riskArr.push(item.label);
              });
              data.value = riskArr.toString();
            }
          });
          res.data.data.controlsRecord.forEach((item) => {
            if (item.controlsId === "workTicket-otherSafeStep")
              this.formData.helpSafetyField = item.value;
          });
        });
    },

    // ------
    timeProcess(time) {
      const date = new Date(time); //date131000
      const SY = date.getFullYear();
      const SM =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      const SD = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      const Sh = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      const Sm =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      return SY + "" + SM + "" + SD + "" + Sh + "" + Sm + "";
    },

    /* pdf */
    onExport() {
      const obj = {
        // type: this.row.type,
        // applyStaffName: this.formData.applyStaffName,
        // workOrder: this.formData.workOrder,

        type: "test",
        workOrder: "1",
      };
      const exportContainer = document.getElementById("imageWrapper");
      downloadPDF(exportContainer, obj);
    },
  },
  created() {},
  mounted() {
    this.getFromData();
  },
};
</script>

